const randomNumber = (min, max, precision = 0) => {
  const factor = 10 ** precision;
  const preciseMin = min * factor;
  const preciseMax = max * factor;
  const number = Math.floor((Math.random() * (preciseMax - preciseMin + 1)) + preciseMin) / factor;

  return number;
};

const formatNumber = (number) => {
  const numAsString = number.toString();
  const div3 = Math.floor(numAsString.length / 3);
  const mod3 = numAsString.length % 3;

  let returnValue = numAsString.substring(0, mod3);
  for (let i = 0; i < div3; i += 1) {
    const start = mod3 + 3 * i;
    const end = start + 3;
    const seperatorOn = (i > 0 && div3 > 0) || mod3 > 0;
    returnValue += `${seperatorOn ? ',' : ''}${numAsString.substring(start, end)}`;
  }

  return returnValue;
};

const formatCurrency = (number) => {
  return `$${number.toFixed(2)}`;
};

const formatPriceString = (items) => {
  let priceString = '';
  items.forEach((item, index) => {
    if (index === 0) {
      priceString = formatCurrency(item);
    } else if (index < items.length - 1) {
      priceString += `, ${formatCurrency(item)}`;
    } else {
      priceString += ` and ${formatCurrency(item)}`;
    }
  });

  return priceString;
};

export default {
  randomNumber,
  formatNumber,
  formatCurrency,
  formatPriceString,
}
