import React from 'react';

import HistoryItem from './HistoryItem';
import AppContext from '../../contexts/AppContext';

const renderHistoryItems = history => (
  history.slice(0).reverse().map(result => (
    <HistoryItem
      key={result.number}
      number={result.number}
      question={result.string}
      answer={result.answer}
      userAnswer={result.userAnswer}
      correct={result.correct}
    />
  ))
);

const History = () => {
  const appContext = React.useContext(AppContext);

  return (
    <table className="history">
      <tbody>
        <tr>
          <th className="history-number">#</th>
          <th className="history-question">Question</th>
          <th className="history-answer">Expected Answer</th>
          <th className="history-answer">You Answered</th>
          <th className="history-correct">Correct?</th>
        </tr>
        { renderHistoryItems(appContext.history) }
      </tbody>
    </table>
  );
};

export default History;
