import React from 'react';
import PropTypes from 'prop-types';

const Operation = ({
  index,
  operation,
  onChange,
}) => {
  const { symbol, min, max, active } = operation;

  const handleActiveChange = () => {
    operation.active = !operation.active;
    onChange(operation);
  };

  const handleMinChange = (event) => {
    const { value } = event.target;
    operation.min = value.length > 0 ? Number.parseInt(value, 10) : 0;
    onChange(operation);
  };

  const handleMaxChange = (event) => {
    const { value } = event.target;
    operation.max = value.length > 0 ? Number.parseInt(value, 10) : 0;
    onChange(operation);
  };

  return (
    <div className="config-row">
      <div className="config-row-item">
        <label htmlFor={`active-${index}`} className="switch">
          <input
            id={`active-${index}`}
            type="checkbox"
            onChange={handleActiveChange}
            checked={active}
          />
          <span className="slider round" />
        </label>
      </div>
      <div className="config-row-item">
        <span className="symbol">
          {symbol}
        </span>
      </div>
      <div className="config-row-item">
        <label htmlFor="min">
          <span className="label-text">
            {'Min:'}
          </span>
          <input
            className="medium-integer"
            id={`min-${index}`}
            type="number"
            value={min.toString()}
            onChange={handleMinChange}
          />
        </label>
      </div>
      <div className="config-row-item">
        <label htmlFor="max">
          <span className="label-text">
            {'Max:'}
          </span>
          <input
            className="medium-integer"
            id={`max-${index}`}
            type="number"
            value={max.toString()}
            onChange={handleMaxChange}
          />
        </label>
      </div>
    </div>
  );
};

Operation.propTypes = {
  index: PropTypes.number.isRequired,
  operation: PropTypes.shape({
    symbol: PropTypes.string,
    min: PropTypes.number,
    max: PropTypes.number,
    active: PropTypes.bool,
  }),
  onChange: PropTypes.func,
};

Operation.defaultProps = {
  operation: {
    symbol: '',
    min: 0,
    max: 0,
    active: true,
  },
  onChange: () => {},
};

export default Operation;
