import React from 'react';

import AppContext from '../../contexts/AppContext';

class QuestionForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      answer: '',
      displayResult: false,
    };
  };

  static contextType = AppContext;
  
  createResult = (question, userAnswer) => (
    {
      ...question,
      userAnswer,
      correct: userAnswer === question.answer,
    }
  );

  checkAnswer = (result) => {
    if (result.correct) {
      return {
        resultString: 'You are correct',
        displayResult: true,
        correct: result.correct,
      };
    }
    return {
      resultString: 'That is not the correct answer',
      displayResult: true,
      correct: result.correct,
    };
  }

  handleChange = (event) => {
    const regex = /(\s)|(^[-]?[\d]*[.]?[\d]*)$/;
    const value = event.target.value.trim();

    if (regex.test(value)) {
      this.setState({
        answer: value,
        displayResult: false,
      });
    }
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const result = this.createResult(this.context.question, Number(this.state.answer));
    this.setState(this.checkAnswer(result));

    setTimeout(() => {
      this.setState({
        answer: '',
        displayResult: false,
      })
      this.props.onSubmit(result);
    }, this.context.config.timeBetweenQuestions);
  }

  render() {
    const { question } = this.context;
    const { correct, displayResult, resultString } = this.state;

    return question.string ? (
      <form className="question-container" onSubmit={this.handleSubmit}>
        <div className="question">
          {`${question.string}`}
        </div>
        <div className="answer">
          <input
            type="text"
            value={this.state.answer}
            onChange={this.handleChange}
          />
          <input
            type="submit"
            value="Submit"
          />
        </div>
        <div className={`result ${correct ? 'correct' : ''}`}>
          { displayResult
            ? resultString
            : null
          }
        </div>
      </form>
    ) : null;
  };
};

export default QuestionForm;
