import React, { Fragment } from 'react';
import { Icon, Modal, Button } from 'semantic-ui-react';

import Operation from './Operation';
import AppContext from '../../contexts/AppContext';

const Config = () => {
  const [open, setOpen] = React.useState(false);
  const [hasChange, setHasChange] = React.useState(false);
  const { config, setConfig, createQuestion } = React.useContext(AppContext);

  // event handler for opening configuration modal
  const handleConfigOpen = () => {
    setOpen(true);
  };

  // event handler for closing configuration modal
  const handleConfigClose = () => {
    setOpen(false);

    // if configuration has changed, create a new question
    if (hasChange) {
      createQuestion();
      setHasChange(false);
    };
  };

  // event handler for an operation change
  const handleOperationChange = (operation) => {
    // find and update the operation that has changed
    const index = config.operations.findIndex((item) => item.symbol === operation.symbol);
    config.operations[index] = operation;
    setConfig(config);
    setHasChange(true);
  };

  return (
    <Fragment>
      <Icon name="cog" onClick={handleConfigOpen} />
      <Modal size="tiny" open={open} onClose={handleConfigClose}>
        <Modal.Header className="center">Config</Modal.Header>
        <Modal.Content>
          <Fragment>
            {config.operations.map((operation, index) => (
              <Operation
                index={index}
                key={operation.symbol}
                operation={operation}
                onChange={handleOperationChange}
              />
            ))}
          </Fragment>
        </Modal.Content>
        <Modal.Actions>
          <Button onClick={handleConfigClose}>Close</Button>
        </Modal.Actions>
      </Modal>
    </Fragment>
  );
};

export default Config;
