import Operation from './Operation';
import utils from '../../utils';

class DivisionOperation extends Operation {
  createQuestion = () => {
    const answer = utils.randomNumber(this.min, this.max);
    let denominator = utils.randomNumber(this.min, this.max);
    while (denominator === 0) {
      denominator = utils.randomNumber(this.min, this.max);
    }
    const numerator = answer * denominator;
    const string = `${utils.formatNumber(numerator)} ${this.symbol} ${utils.formatNumber(denominator)}`;
  
    return {
      string,
      answer,
    };
  };
}

export default DivisionOperation;
