import Operation from './Operation';
import utils from '../../utils';

class MoneyOperation extends Operation {
  constructor(params) {
    super(params);
    this.maxItems = params.maxItems;
  }

  findMinCash = (total) => {
    const denominations = [1, 2, 5, 10, 20, 50, 100];
    let cash = 0;
  
    const predicate = (value) => {
      return (value === 100 || value > (total - cash));
    };
  
    while (cash < total) {
      cash += denominations.find(predicate);
    }
  
    return cash;
  };

  createQuestion = () => {
    const factor = 10 ** this.decimals;
    const numItems = utils.randomNumber(1, this.maxItems);
    const items = [];
    let total = 0;
    for (let i = 0; i < numItems; i += 1) {
      items[i] = utils.randomNumber(this.min, this.max, this.decimals);
      total += items[i];
    }
    const cashPaid = this.findMinCash(total);
    const answer = Math.round((cashPaid - total) * factor) / factor;
    const string = `You want to purchase ${numItems} `
      + `${(numItems === 1 ? 'item with price' : 'items with prices')} of `
      + `${utils.formatPriceString(items)}. You give the cashier ${utils.formatCurrency(cashPaid)}, `
      + 'how much change should you expect?';

    return {
      string,
      answer,
    };
  }
}

export default MoneyOperation;
