import _ from 'lodash';
import React from 'react';

import AppContext from '../../contexts/AppContext';

const Score = () => {
  const appContext = React.useContext(AppContext);
  const numberCorrect = appContext.history.reduce((acc, result) => {
    return (result.correct ? acc + 1 : acc);
  }, 0);
  const totalQuestions = appContext.history.length;

  const correctVsTotal = `${numberCorrect}/${totalQuestions}`;
  const score = totalQuestions > 0 ? numberCorrect / totalQuestions : 0;
  const scoreAsPercent = _.round(score * 100, 2);
  return (
    <div className="score">
      {`Score: ${scoreAsPercent}% (${correctVsTotal})`}
    </div>
  );
};

export default Score;
