import utils from '../../utils';

class Operation {
  constructor({ symbol, active, min, max, decimals }) {
    this.symbol = symbol;
    this.active = active;
    this.min = min;
    this.max = max;
    this.decimals = decimals;
  };

  /*
   * method: createQuestion
   * purpose: generate a question where the answer is simple to determine
   *   ie) a + b
   * returns: the question generated
   */
  createQuestion(accumulator) {
    const { randomNumber, formatNumber } = utils;
    const numbers = [];

    // create required random numbers
    for (let i = 0; i < 2; i += 1) {
      numbers[i] = randomNumber(this.min, this.max, this.decimals);
    }

    // generate the question string
    const string = `${formatNumber(numbers[0])} ${this.symbol} ${formatNumber(numbers[1])}`;

    // calculate the answer
    const answer = numbers.reduce(accumulator);

    return {
      string,
      answer,
    };
  };
}

export default Operation;
