import React from 'react';

import Operations from './Operations';
import utils from '../utils';

const Context = React.createContext();

// constant parameters for the operations and their default settings
const ADDITION_PARAMS = { symbol: '+', min: 0, max: 10, decimals: 0, active: true };
const SUBTRACTION_PARAMS = { symbol: '-', min: 0, max: 10, decimals: 0, active: true };
const MULTIPLICATION_PARAMS = { symbol: 'ร', min: 0, max: 10, decimals: 0, active: true };
const DIVISION_PARAMS = { symbol: 'รท', min: 0, max: 10, decimals: 0, active: true };
const MONEY_PARAMS = { symbol: '$', min: 0, max: 10, decimals: 2, active: true, maxItems: 3 };

// setup the defalut configuration object
const DEFAULT_CONFIG = {
  operations: [
    new Operations.AdditionOperation(ADDITION_PARAMS),
    new Operations.SubtractionOperation(SUBTRACTION_PARAMS),
    new Operations.MultiplicationOperation(MULTIPLICATION_PARAMS),
    new Operations.DivisionOperation(DIVISION_PARAMS),
    new Operations.MoneyOperation(MONEY_PARAMS),
  ],
  timeBetweenQuestions: 1500,
};

export class AppStore extends React.Component {
  // state that hold all application data
  state = {
    config: DEFAULT_CONFIG,
    history: [],
    question: {},
  }

  componentDidMount() {
    this.createQuestion();
  }

  // methods to update application state
  setConfig = (config) => this.setState({ config });
  setQuestion = (question) => this.setState({ question });

  // method to create a new question
  createQuestion = () => {
    let question = {};
    const { operations } = this.state.config;

    // get an array of operations that are active
    const activeOperations = operations.reduce((acc, cur) => {
      if (cur.active) {
        acc.push(cur);
      }
      return acc;
    }, []);

    // if there are some active operations, create a new question
    if (activeOperations.length > 0) {
      const index = utils.randomNumber(0, activeOperations.length - 1, 0);
      question = activeOperations[index].createQuestion();
    }

    this.setQuestion(question);
  };

  addResultToHistory = (result) => {
    this.setState((prevState) => ({
      history: [
        ...prevState.history,
        {
          ...result,
          number: prevState.history.length + 1,
        },
      ],
    }));
  };

  render() {
    const value = {
      config: this.state.config,
      setConfig: this.setConfig,
      history: this.state.history,
      addResultToHistory: this.addResultToHistory,
      question: this.state.question,
      createQuestion: this.createQuestion,
      setQuestion: this.setQuestion,
    };

    return (
      <Context.Provider value={value}>
        {this.props.children}
      </Context.Provider>
    );
  }
};

export default Context;
